<h2 id="xcode-command-line-tools" tabindex="-1"><a class="header-anchor" href="#xcode-command-line-tools" aria-hidden="true">#</a> Xcode Command Line Tools</h2>
<p>Xcode is a large suite of software development tools and libraries from apple.
Xcode Command Line Tools are part of Xcode
Installation of many common Unix-based tools requires the <code>GCC compiler</code>,The Xcode Command Line Tools include a GCC compiler</p>
<h2 id="is-xcode-already-installed" tabindex="-1"><a class="header-anchor" href="#is-xcode-already-installed" aria-hidden="true">#</a> Is Xcode Already Installed?</h2>
<p>You don’t need the full Xcode package to get the Xcode Command Line Tools</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>xcode-select -p
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="install-xcode-command-line-tools" tabindex="-1"><a class="header-anchor" href="#install-xcode-command-line-tools" aria-hidden="true">#</a> Install Xcode Command Line Tools</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>xcode-select --install
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>Verify that you’ve successfully installed Xcode Command Line Tools:</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>xcode-select -p
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>gcc --version
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="the-relationship-between-homebrew-and-xcode-command-line-tools" tabindex="-1"><a class="header-anchor" href="#the-relationship-between-homebrew-and-xcode-command-line-tools" aria-hidden="true">#</a> The relationship between homebrew and xcode command line tools</h2>
<h2 id="reinstall-xcode-command-line-tools" tabindex="-1"><a class="header-anchor" href="#reinstall-xcode-command-line-tools" aria-hidden="true">#</a> ReInstall Xcode Command Line Tools</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">sudo</span> <span class="token function">rm</span> -rf /Library/Developer/CommandLineTools
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>删除文件夹后执行：gcc -v</p>
<p>xcrun: error: active developer path (&quot;/Library/Developer/CommandLineTools&quot;) does not exist
Use <code>sudo xcode-select --switch path/to/Xcode.app</code> to specify the Xcode that you wish to use for command line developer tools, or use <code>xcode-select --install</code> to install the standalone command line developer tools.
See <code>man xcode-select</code> for more details.</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>xcode-select --install
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="library-developer-commandlinetools" tabindex="-1"><a class="header-anchor" href="#library-developer-commandlinetools" aria-hidden="true">#</a> /Library/Developer/CommandLineTools</h2>
